$(document).ready(function () {

    /* Movie Caorusel */
    $("#shows-wrapper").owlCarousel({
        items: 8,
        loop: true,
        slideBy: 2,
        center: true,
        autoplay: true,
        smartSpeed: 250,
        autoplayTimeout: 3000,
        slideTransition: 'ease-in-out',
        responsive: {
            0: {
                items: 2
            },
            622: {
                items: 4,
            },
            768: {
                items: 4
            },
            1200: {
                items: 6
            },
            1441: {
                items: 8
            }
        }
    });

    /* UI Tabs */
    $(".ui-features__tab li").click(function () {
        $(".ui-features__tab li").removeClass("active");
        $(this).addClass("active");
    })

    gsap.registerPlugin(ScrollTrigger);

    if ($(window).innerWidth() >= 992) {
        const tl = gsap.timeline({
            scrollTrigger: {
                trigger: ".ui-features",
                start: "center",
                endTrigger: ".pre-footer",
                scrub: true
            }
        });

        const tl2 = gsap.timeline({
            scrollTrigger: {
                trigger: ".ui-features",
                start: "center",
                endTrigger: ".pre-footer",
                scrub: true
            }
        });
        tl.to(".tweet-inner.left", { y: 300, duration: 1 })
        tl2.to(".tweet-inner.right", { y: -300, duration: 1 })
    }

});